<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <img class="banner-img" :src="bannerImg" alt="">
      <div class="banner-info">
        <div class="banner-title">{{sightName}}</div>
        <div class="banner-number"><span class="iconfont banner-icon">&#xe626;</span>{{gallaryImgs.length}}</div>
      </div>
    </div>
    <fade-animation>
      <common-gallery
        :imgs="gallaryImgs"
        v-show="showGallery"
        @closeGallery="hideGallery"
      >
      </common-gallery>
    </fade-animation>

  </div>
</template>

<script>
  import commonGallery from 'common/gallery/Gallery'
  import FadeAnimation from 'common/fade/Fade'
  export default {
    name: "DetailBanner",
    props:{
      sightName:String,
      bannerImg:String,
      gallaryImgs:Array
    },
    components:{
      commonGallery,
      FadeAnimation
    },
    data(){
      return{
        showGallery:false
      }
    },
    methods:{
      handleBannerClick(){
        this.showGallery = true;
      },
      hideGallery(){
        this.showGallery = false;
      }
    }
  }
</script>

<style lang="stylus" scoped>

  .banner
    position relative
    height 0
    padding-bottom 55%
    overflow hidden
    .banner-img
      width 100%
    .banner-info
      display flex
      align-items center
      position absolute
      left 0
      right 0
      bottom: 0
      line-height .6rem
      color #fff
      background-image:linear-gradient(top,rgba(0,0,0,0),rgba(0,0,0,0.8))
      .banner-title
        flex: 1
        font-size .32rem
        padding:0 .2rem
      .banner-number
        padding: 0 .4rem
        line-height .5rem
        border-radius .2rem
        background-color :rgba(0,0,0,0.8)
        font-size .24rem
        height 0.5rem
        .banner-icon
          font-size .24rem
          margin-right: .1rem

</style>



