<template>
    <div class="container" @click="handleGalleryClick">
      <div class="wrapper" >
        <swiper :options="swiperOption">
          <!-- slides -->
          <swiper-slide v-for="(item,index) in imgs" :key="index">
            <img class="gallery-img" :src="item"/>
          </swiper-slide>

          <!-- Optional controls -->
          <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
      </div>

    </div>
</template>

<script>
  export default {
	  name: "CommonGallery",
    props:{
	    imgs:{
	      type:Array,
        default(){
	        return []
        }
      }
    },
    data(){
	    return {
        swiperOption: {
          pagination: '.swiper-pagination',
          paginationType : 'fraction',
          observer:true,
          observeParents:true,
        }
      }
    },
    methods:{
      handleGalleryClick(){
        this.$emit('closeGallery')

      }
    }
  }
</script>

<style lang="stylus" scoped>
  .container >>> .swiper-container
    overflow inherit
  .container
    z-index:99
    display flex
    flex-direction column
    justify-content center
    position: absolute
    left 0
    right:0
    top:0
    bottom: 0
    background-color #000
    .wrapper
      /*background-color #fff*/
      /*overflow :hidden*/
      width:100%
      /*height 0*/
      /*padding-bottom: 100%*/
      .gallery-img
        width: 100%
      .swiper-pagination
        color:#fff
        bottom:-1rem


</style>



