<template>
  <div class="header">
    <router-link to="/">
      <div class="header-left">
        <span class="iconfont back-icon">&#xe624;</span>
      </div>
    </router-link>

    <span class="city-title">城市选择</span>
    <span></span>
  </div>
</template>

<script>
  export default {
	  name: "Header",
  }
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .header
    line-height : $headerHeight
    display : flex
    align-items : center
    background : $bgColor
    color: #ffffff
    text-align center
    position relative
    .header-left
      margin-left 5px
      color:#fff;
    .city-title
      position absolute;
      margin-left 50%;
      transform translateX(-50%)

</style>



