<template>
    <div>
      <div class="recommend-title">
        热销推荐
      </div>
      <ul>
          <router-link tag="li" :to="'/detail/'+item.id" class="item" v-for="item of list" :key="item.id">
            <div class="item-img-wrapper">
              <img class="item-img" :src="item.imgUrl" alt="">
            </div>
            <div class="item-info">
              <p class="item-title">{{item.title}}</p>
              <p class="item-desc">{{item.desc}}</p>
              <button class="item-button">查看详情</button>
            </div>
          </router-link>
      </ul>
    </div>
</template>

<script>
export default {
  name: "HomeRecommed",
  props:{
    list:Array
  }
}
</script>

<style lang="stylus" scoped>
  @import "~styles/mixins.styl"
  .recommend-title
    line-height .8rem
    background-color: #eee
    text-indent :0.2rem
    margin-top .2rem
  .item
    overflow :hidden
    display: flex
    height:1.9rem
    .item-img
      width:1.7rem
      height: 1.7rem
      padding:.1rem
    .item-info
      flex:1
      padding:.1rem
      align-content  center
      min-width: 0
      .item-title
        line-height: .54rem
        font-size: .32rem
      .item-desc
        line-height: .4rem
        color:#ccc
        ellipse()
      .item-button
        margin-top .16rem
        background-color: #ff9300
        padding: 0 .2rem
        border-radius :.06rem
        color:#fff





</style>



