<template>
  <div>
    <router-link tag="div" to="/" class="header-abs" v-if="showAbs"><span class="iconfont header-abs-back">&#xe624;</span></router-link>
    <div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
      <router-link to="/">
        <div class="header-left">
          <span class="iconfont back-icon">&#xe624;</span>
        </div>
      </router-link>
      <span class="city-title" >景点详情</span>
      <span></span>
    </div>
  </div>
</template>

<script>

  export default {
    name: "DetailHeader",
    data(){
      return{
        showAbs:true,
        opacityStyle:{
          opacity:0
        }
      }
    },
    methods:{
      handleScroll(){
        const top = document.documentElement.scrollTop;
        if(top > 30 ) {
          let opacity = top/140;
          opacity = opacity>1?1:opacity;
          this.opacityStyle={opacity};
          this.showAbs = false;
        }else{
          this.showAbs = true;
          // this.opacityStyle={opacity:0};
        }
      }
    },
    // activated() {
    //   console.log('activated-header');
    //   window.addEventListener('scroll',this.handleScroll)
    // },
    // deactivated() {
    //   window.removeEventListener('scroll',this.handleScroll)
    // },
    mounted(){
      window.addEventListener('scroll',this.handleScroll)
    }
  }
</script>

<style lang="stylus" scoped>
  @import "~styles/varibles.styl"
  .header-abs
    position: absolute
    left:.2rem
    top:.2rem
    width:.8rem
    height:.8rem
    line-height .8rem
    text-align center
    border-radius .8rem
    background-color rgba(0,0,0,.8)
    z-index 10
    .header-abs-back
      font-size .4rem
      color: #ffffff
  .header-fixed
    position fixed
    line-height : $headerHeight
    display : flex
    align-items : center
    background : $bgColor
    color: #ffffff
    text-align center
    top:0
    left:0
    right:0
    z-index 1
    .header-left
      margin-left 5px
      color:#fff;
    .city-title
      position absolute;
      margin-left 50%;
      transform translateX(-50%)

</style>



