<template>
  <div class="header">
    <div class="header-left">
      <span class="iconfont back-icon">&#xe624;</span>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe632;</span>
      输入景点/城市/游玩/主题
    </div>
    <router-link to="/city">
      <div class="header-right">
        <span class="city-icon-text">{{this.city}}</span>
        <span class="iconfont arrow-icon">&#xe64a;</span>
      </div>
    </router-link>
  </div>

</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: "HomeHeader",
    computed:{
      ...mapState(['city'])
    },
    props: {
      cityName:String
    }
  }
</script>

<style lang="stylus" scoped>
//@import "~@/assets/style/varibles.styl"
@import "~styles/varibles.styl"
  /*移动端布局是rem布局，html：font-size：50px; 设计图上height:86px;86/2=43,43/50=0.86rem*/
  .header
    line-height : $headerHeight
    display : flex
    align-items : center
    background : $bgColor
    color: #ffffff
    .header-left
      width: .64rem
      text-align : center
      .back-icon
        font-size : .4rem
        text-align : center
    .header-input
      flex: 1
      /*margin-top: .12rem*/
      height: .64rem
      line-height : .64rem
      margin-left: .2rem
      padding-left : .2rem
      background :#fff
      border-radius :.1rem
      color:#ccc
    .header-right
      width: 1.24rem
      height: .96rem
      line-height: .96rem
      text-align: center
      font-size : 0
      color:#fff
      .city-icon-text
        font-size : .3rem
      .arrow-icon
        font-size : .24rem


</style>



